@import '../../../../../scss/settings/font';
@import '../../../../../scss/settings/colors';
@import './src/scss/settings/mixins';

.formContainer {
  .stepContainer {
    .selectContainer {
      margin-top: 5rem;
      display: flex;
      align-items: center;

      p {
        font-size: 2.4rem;
        font-weight: 300;
      }

      .select {
        width: 30rem;
        margin-left: 2.6rem;
        font-size: 2.4rem;
        font-weight: 300;
        @include border(1px, $fontColor, 0);

        &:focus {
          outline: none;
        }

        .css-1pahdxg-control {
          background-color: transparent;
          box-shadow: none;
          border: none;
        }

        .css-yk16xz-control {
          background-color: transparent;
          border: none;
          border-radius: 0;

          div:focus {
            outline: none;
          }

          .css-1wa3eu0-placeholder {
            color: $fontColor;
            font-size: 2.4rem;
            font-family: $font-base;
          }
        }

        .css-26l3qy-menu {
          width: 8.5rem;
          right: 0;
          background-color: transparent;
          @include border(1px, $fontColor, 0);

          .css-4ljt47-MenuList {
            padding: 0;

            div {
              padding: .5rem 0;
              font-size: 2.4rem;
              text-align: center;

              &:hover {
                background-color: $decorationColor;
              }
            }
          }
        }
      }
    }
  }

  .formFooter {
    .errorStep2{
      margin: 15% 0 0 10%;
    }
    .step2Button {
      margin-top: 48.3%;
      margin-right: 5rem;
    }
  }
}